.banner-img {
    margin-top: 60px;
  }
  
  .home-page h1 {
    color: gray;
    font-family: "Playfair Display", serif;
  }
  
  .home-page {
    display: flex;
    flex-wrap: wrap;
  }
  .home-page .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
  }
  .home-page .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .home-page .card-price {
    color: green;
    font-weight: bold;
  }
  .home-page button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }
  
  .home-page .card-text {
    color: rgb(90, 89, 89);
  }
  
  .home-page .card img:hover {
    transform: scale(0.9);
  }
  
  .home-page .card-img-top {
    height: 300px !important;
  }
  /* ==================
  filters
  ============== */
  .filters h4 {
    margin-top: 100px;
    color: gray !important;
    border-bottom: 1px solid rgba(128, 128, 128, 0.337);
  }
  
  .ant-checkbox-wrapper:first-of-type {
    margin-left: 8px;
  }
  
  .ant-checkbox-wrapper {
    margin: 2px;
  }
  .ant-radio-wrapper {
    margin-bottom: 5px !important;
    margin-left: 10px;
  }
  
  .filters button {
    background-color: black;
    width: 100%;
    border: none;
    border-radius: 0;
    margin-top: 20px;
  }
  
  .loadmore {
    color: green;
    font-weight: bold;
    font-size: 20px !important;
  }

  /* Add additional styles */
.home-page .card:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.home-page .card-name-price {
  margin-top: 10px; /* Adjust spacing */
}

/* Responsive styles using media queries */
@media screen and (max-width: 768px) {
  .home-page {
      flex-direction: column; /* Stack cards vertically on smaller screens */
  }

  .home-page .card {
      width: 100%; /* Make cards full width on smaller screens */
  }

  .home-page .card-img-top {
      height: 200px !important; /* Adjust image height for smaller screens */
  }

  .home-page h1 {
      font-size: 24px; /* Decrease font size for smaller screens */
  }
}

/* Additional responsive styles for even smaller screens */
@media screen and (max-width: 480px) {
  .home-page h1 {
      font-size: 20px; /* Further decrease font size for very small screens */
  }

  .home-page .card-text {
      font-size: 12px; /* Adjust text size for smaller screens */
  }

  .home-page button {
      font-size: 12px; /* Decrease button font size for smaller screens */
  }

  .filters h4 {
      margin-top: 50px; /* Adjust heading margin for smaller screens */
  }
}
