/* Base Styles for Category Section */
.category {
  display: flex;
  flex-wrap: wrap;
  margin-top: 80px !important;
}

.category .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
  margin: 10px; /* Add margin for spacing between cards */
  border-radius: 5px; /* Add border radius to cards */
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2); /* Add a subtle box shadow to cards */
}

.category .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px; /* Add padding to card name and price section */
}

.category .card-price {
  color: green;
  font-weight: bold;
}

.category button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
  background-color: #000; /* Button background color */
  color: #fff; /* Button text color */
  border: none;
  padding: 10px; /* Add padding to buttons */
  cursor: pointer;
  transition: background-color 0.3s ease; /* Add button hover effect */
}

.category button:hover {
  background-color: #434343; /* Button background color on hover */
}

.category .card-text {
  color: rgb(90, 89, 89);
  padding: 10px; /* Add padding to card text */
}

.category .card img:hover {
  transform: scale(0.9);
}

.category .card-img-top {
  height: 300px !important;
}

/* Responsive Styles for Category Section */
@media screen and (max-width: 768px) {
  .category .card {
    width: calc(50% - 20px); /* Display two cards per row on smaller screens with spacing */
  }
}

@media screen and (max-width: 480px) {
  .category .card {
    width: calc(100% - 20px); /* Display one card per row on even smaller screens with spacing */
  }
}
