/* Base Styles for Cart Page */
.cart-page {
  margin-top: 64px;
  padding: 20px;
  font-family: "roboto", sans-serif;
}

.cart-page h1 {
  font-weight: normal;
  background-color: rgba(0, 0, 255, 0.072);
  padding: 10px 20px;
  text-align: center;
}

.cart-page h1 > p {
  font-size: 18px;
  margin-top: 10px;
  text-align: center;
}

.cart-page .row {
  display: flex;
  flex-wrap: wrap;
}

.cart-page .card {
  padding: 10px;
  height: 180px;
  margin-bottom: 20px;
  display: flex;
}

.cart-page .cart-remove-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart-page .cart-summary {
  text-align: center;
}

/* Responsive Styles */
@media screen and (max-width: 768px) {
  .cart-page h1 {
    font-size: 24px;
  }

  .cart-page h1 > p {
    font-size: 16px;
  }

  .cart-page .card {
    height: auto;
    flex-direction: column;
  }

  .cart-page .col-md-7 {
    width: 100%;
  }

  .cart-page .col-md-5 {
    width: 100%;
    margin-top: 20px;
  }
}

@media screen and (max-width: 576px) {
  .cart-page h1 {
    font-size: 20px;
  }

  .cart-page h1 > p {
    font-size: 14px;
  }

  .cart-page .card {
    height: auto;
    flex-direction: column;
  }
}
