
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Font Family */
/* Uncomment and use fonts as needed */
/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* Navbar Styles */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
  background: linear-gradient(to right, #00b894, #00cec9); /* Greenish Gradient Background */
}
.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
  color: #fff; /* Text color for the navbar brand */
}

/* Footer Styles */
.footer {
  color: white;
  padding: 25px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #00b894, #00cec9); /* Greenish Gradient Background */
  background: linear-gradient(to right, #00b894, #00cec9); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* Page Not Found Styles */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.pnf-btn:hover {
  background-color: black;
  color: white;
}

/* Contact Us Styles */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Register Styles */
.register {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;
}

/* Product Link Styles */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}

.cat-btn:hover {
  background-color: #434343;
  color: white;
}

/* Dashboard Styles */
.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color: #434343 !important;
  color: white;
  padding: 20px 0px;
}

/* Responsive Styles */
@media screen and (max-width: 768px) {
  .navbar {
    font-size: 14px;
  }

  .pnf-title {
    font-size: 60px;
  }

  .cat-btn {
    font-size: 20px;
    padding: 20px 0px;
  }
}

/* Additional responsive styles for smaller screens */
@media screen and (max-width: 480px) {
  .navbar {
    font-size: 12px;
  }

  .pnf-title {
    font-size: 40px;
  }

  .cat-btn {
    font-size: 16px;
    padding: 10px 0px;
  }
}

/* Additional Styles */

/* Add a hover effect for navbar links */
.nav-link:hover {
  color: #434343;
}

/* Add a border radius to the buttons */
.btn {
  border-radius: 5px;
}

/* Style the Page Not Found button */
.pnf-btn {
  background-color: #000;
  color: #fff;
  transition: background-color 0.3s ease;
}

.pnf-btn:hover {
  background-color: #434343;
}

/* Style the Contact Us form */
.contact-form {
  max-width: 400px;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

/* Style the Register form */
.register-form {
  max-width: 400px;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

/* Style the buttons within the forms */
.form-btn {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.form-btn:hover {
  background-color: #434343;
}

/* Add a subtle box shadow to images */
img {
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

/* Style the footer links */
.footer a {
  text-transform: uppercase;
  font-weight: 500;
}

/* Style the Load More button */
.loadmore {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.loadmore:hover {
  background-color: #434343;
}

/* Add a subtle border to the cat-btn on hover */
.cat-btn:hover {
  border: 1px solid #434343;
}
